/* src/App.css */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Make it full height */
  background: linear-gradient(to bottom, rgba(255, 0, 0, 0.8), rgba(0, 0, 255, 0.8)); /* Reddish-blue gradient */
  color: white; /* Change text color to white for better visibility */
  font-family: Arial, sans-serif; /* Font for better readability */
}

h1, h3 {
  text-align: center;
}

h4 {
  margin: 10px 0; /* Space out messages */
}
